import React from 'react';
import { Button, Table, Icon, Header, Image, Modal} from 'semantic-ui-react';
import ScoreButton from '../components/scoreButton';
import ToggleButton from '../components/toggleButton';

class CellCluster extends React.Component{

    constructor(props){
        super(props);
                
        let nd =[];
        for(let i=0;i<this.props.rownum;i+=1){
           let row = [];
           for(let j=0;j<this.props.columnnum;j+=1){
              row.push(this.props.data[i*this.props.columnnum+j]);
           } 
           nd.push(row); 
        }

        this.state = ({ clickCount : 0,
                        data: nd,
                        rownum: this.props.rownum,
                        columnnum: this.props.columnnum,
                        clustername: this.props.clustername,
                        cellcolumn: this.props.cellcolumn
                     });
         
    }
    componentDidMount() {
    }   

    componentDidUpdate(){
        
    }

    updateInputValue(evt) {

      this.setState({
        inputValue: evt.target.value
      });
    }

    popupActive(){
      if (this.state.modalTrigger===false){
        this.setState({
          modaltrigger: true
        });
      }     
    }

    render(){

    const celltable = this.state.data.map((row,i) => {
      return(
              <Table.Row key={i}>
                {row.map((each,j)=>{
                  return(
                    <Table.Cell key={j}>
                      
                      
                      <Modal size='large' trigger={
                          <Button  size='mini' animated color={ each.done ?'green':'grey'}>
                            <Button.Content visible>
                              <div>{ each.groupName}</div>
                              <div>{this.state.clustername+'-'+each.seatNumber}</div>
                            </Button.Content>
                            <Button.Content hidden>
                              <Icon name='arrow up' />
                            </Button.Content>
                          </Button>
                        }>
                        <Modal.Header>{each.groupName}</Modal.Header>
                        <Modal.Content image>
                          <Image wrapped size='large' src='https://react.semantic-ui.com/images/avatar/large/rachel.png' />
                          <Modal.Description>
                            <Header>目前評分</Header>
                            <Table>
                            <Table.Body>
                                <Table.Row key={each.id+'-r1'}>
                                  <Table.Cell>技術分數</Table.Cell>
                                  <Table.Cell key={each.id+'-1'}><ScoreButton  id={each.id} column={this.state.cellcolumn} scoreValue={each.scoreTech}></ScoreButton></Table.Cell>
                                </Table.Row>
                                <Table.Row key={each.id+'-r2'}>
                                  <Table.Cell>創意分數</Table.Cell>
                                  <Table.Cell key={each.id+'-2'}><ScoreButton id={each.id} column={this.state.cellcolumn+1} scoreValue={each.scoreCreative}></ScoreButton></Table.Cell>
                                </Table.Row>
                                <Table.Row key={each.id+'-r3'}>
                                  <Table.Cell>簡報分數</Table.Cell>
                                  <Table.Cell key={each.id+'-3'}><ScoreButton key={each.id} id={each.id} column={this.state.cellcolumn+2} scoreValue={each.scorePresent}></ScoreButton></Table.Cell>
                                </Table.Row>
                                <Table.Row key={each.id+'-r4'}>
                                  <Table.Cell>設計分數</Table.Cell>
                                  <Table.Cell key={each.id+'-4'}><ScoreButton key={each.id} id={each.id} column={this.state.cellcolumn+3} scoreValue={each.scoreDesign}></ScoreButton></Table.Cell>
                                </Table.Row>
                                <Table.Row key={each.id+'-r5'}>
                                  <Table.Cell>創造價值分數</Table.Cell>
                                  <Table.Cell key={each.id+'-5'}><ScoreButton key={each.id} id={each.id} column={this.state.cellcolumn+4} scoreValue={each.scoreValue}></ScoreButton></Table.Cell>
                                </Table.Row>
                              </Table.Body>
                            </Table>
                          </Modal.Description>
                          <Modal.Actions>
                            <ToggleButton key={each.id+'-6'} active={each.done}></ToggleButton>
                          </Modal.Actions>
                        </Modal.Content>
                      </Modal>


                    </Table.Cell>
                  );
                  })
                }
              </Table.Row>
      );
    });


    const cell = () => {
      return(
        <Table>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell textAlign='center' colSpan={this.state.columnnum}>{this.state.clustername}</Table.HeaderCell>
            </Table.Row>
          </Table.Header>
          <Table.Body >
           {celltable}
          </Table.Body> 
        </Table>
      );
    }
    return (
          <div>
            {cell()}
          </div>
        );
              
    }
}

export default CellCluster;