import React from 'react';
import { Table, Grid} from 'semantic-ui-react';
import CellCluster from '../components/cellCluster';

class scoreTableTwo extends React.Component {

  //******** construct ********
  constructor(props) {
    super(props);
    this.state = {
        content : null
    };
  }

  componentDidMount() {
    this.getData();
  }

  getData(){
      const vjson = { A:[],
                      B:[],
                      C:[],
                      D:[],
                      E:[]};
      // get data
      fetch('https://spreadsheets.google.com/feeds/list/1G_kFRH_lvFo9gv_atD7VfPYekBCHoqr6-FS5W9eBzW4/od6/public/values?alt=json')
      .then((response) => response.json())
      .then((responseJson) => {
          
          
          for (let i = 0; i < responseJson.feed.entry.length; i+=1) {
            let snv = responseJson.feed.entry[i].gsx$座號.$t;
            let cn = snv.split("-")[0];
            let v = snv.split("-")[1];
            let vd = (responseJson.feed.entry[i].gsx$是否完成二.$t === 'TRUE') ? true : false ;
            let val = {
              'id':responseJson.feed.entry[i].gsx$列值.$t,
              'seatNumber':v,
              'groupName':responseJson.feed.entry[i].gsx$組名.$t,
              'pdf':responseJson.feed.entry[i].gsx$簡報.$t,
              'scoreTech':responseJson.feed.entry[i].gsx$技術分二.$t,
              'scoreCreative':responseJson.feed.entry[i].gsx$創意分二.$t,
              'scorePresent':responseJson.feed.entry[i].gsx$簡報分二.$t,
              'scoreDesign':responseJson.feed.entry[i].gsx$設計分二.$t,
              'scoreValue':responseJson.feed.entry[i].gsx$創造價值分二.$t,
              'done':vd,
            };

            switch (cn){
                case 'A':
                  vjson.A.push(val);
                  break;
                case 'B':
                  vjson.B.push(val);
                  break;
                case 'C':
                  vjson.C.push(val);
                  break;                              
                case 'D':
                  vjson.D.push(val);
                  break;
                case 'E':
                  vjson.E.push(val);
                  break; 
                default :
                  break;                 
            }
            
            
          }
          this.setState({ content :  vjson });
        }
        )
      .catch((error) => {
         console.error(error);
      });
  }

  
  render() {
    
    const ca = (this.state.content == null) ?
                (<Grid></Grid>):
                (<CellCluster data={this.state.content.A} rownum={3} columnnum={3} clustername={'A'} cellcolumn={11}></CellCluster>);
    const cb = (this.state.content == null) ?
                (<Grid></Grid>):
                (<CellCluster data={this.state.content.B} rownum={3} columnnum={3} clustername={'B'} cellcolumn={11}></CellCluster>);
    const cc = (this.state.content == null) ?
                (<Grid></Grid>):
                (<CellCluster data={this.state.content.C} rownum={3} columnnum={3} clustername={'C'} cellcolumn={11}></CellCluster>);
    const cd = (this.state.content == null) ?
                (<Grid></Grid>):
                (<CellCluster data={this.state.content.D} rownum={3} columnnum={3} clustername={'D'} cellcolumn={11}></CellCluster>);
    const ce = (this.state.content == null) ?
                (<Grid></Grid>):
                (<CellCluster data={this.state.content.E} rownum={3} columnnum={4} clustername={'E'} cellcolumn={11}></CellCluster>);            

    //******** render ********

    return (
      <div>
        <Table attached='top' basic>
        </Table>
      

        <Grid celled>
            <Grid.Row >
              <Grid.Column width={4}>
                
              </Grid.Column>
              <Grid.Column width={3}>
                {cc}
              </Grid.Column>
              <Grid.Column width={3}>
                {cb}
              </Grid.Column>
              <Grid.Column width={3}>
                {ca}
              </Grid.Column>
            </Grid.Row>

            <Grid.Row >
              <Grid.Column width={4}>
                {ce}
              </Grid.Column>
              <Grid.Column width={3}>
                {cd}
              </Grid.Column>
              <Grid.Column width={3}>
                
              </Grid.Column>
              <Grid.Column width={3}>
                
              </Grid.Column>
            </Grid.Row>
        </Grid>
      </div>
    );
  }
}

export default scoreTableTwo;