import React from 'react';
import { Button, Input, Icon, Label } from 'semantic-ui-react';
import $ from 'jquery';

class ScoreButton extends React.Component{

    constructor(props){
        super(props);
        
        this.state = ({ clickCount: false,
                        id: this.props.id,
                        column: this.props.column,
                        inputValue: this.props.scoreValue,
                     });

        this.uploadValue = this.uploadValue.bind(this);

        //load googleapi
         
    }
    componentDidMount() {
    }   
    //增加一個count的參數，用來表示每次增加多少
    uploadValue(e){
        const exeurl = 'https://script.google.com/macros/s/AKfycbyFkwIJ944YLmLue5TkIvleLs06FSPdj-iT-qXbqgDVUcA7t88v/exec';
        e.preventDefault();
        
        console.log(this.state.inputValue);
        //this.setState({clickCount:this.state.clickCount + count});
        let datastr=this.state.id+','+this.state.column+','+this.state.inputValue;
        console.log(datastr);
        let uploaddata = {
           data: datastr,
           sheetUrl: 'https://docs.google.com/spreadsheets/d/1G_kFRH_lvFo9gv_atD7VfPYekBCHoqr6-FS5W9eBzW4/edit?usp=sharing',
           sheetTag: 'score'
        }

        $.ajax({
          type: 'GET',
          async: false,
          beforeSend: function(res) {
            res.setRequestHeader('Access-Control-Allow-Origin', '*');
            res.setRequestHeader('Access-Control-Allow-Headers', 'Content-Type');
            res.setRequestHeader('Access-Control-Allow-Credentials', true);
            res.setRequestHeader('Access-Control-Allow-Headers', 'application/json');
          },
          url: exeurl,
          data: uploaddata,
          dataType: 'jsonp',
          cache: false,
          success:() => {

          }
        }).fail(() => {
          if (this.state.inputValue !== 'NA'){
            this.setState({
              clickCount: true
            });
          }
        }); 

    }

    componentDidUpdate(){
        
    }
    updateInputValue(evt) {
      this.setState({
        inputValue: evt.target.value
      });

    }
    
    render(){
        //在onClick中指定觸發的函式後面添加.bind(this)並填上傳入的第一個參數1
        //<input type="button" onClick={this.addCount.bind(this,1)} value="點我" />
        return (
          <div>
            <Button animated onClick={this.uploadValue.bind(this)}>
              <Button.Content visible>Upload</Button.Content>
              <Button.Content hidden>
                <Icon name='arrow up' />
              </Button.Content>
            </Button>
            <Input placeholder={this.state.inputValue} value={this.state.inputValue} onChange={evt => this.updateInputValue(evt)}/>
            <Label basic color={this.state.clickCount?'green':'red'} pointing='left'>
              {this.state.clickCount?'已更新為'+this.state.inputValue:'尚未更新'}
            </Label>
          </div>
        );
              
    }
}

export default ScoreButton;